.card-body {
	color: white;
	border-radius: 5px;
	background:	-webkit-gradient(linear, left top, right bottom, from(rgba(0, 0, 0, 0.76)), to(rgba(0, 0, 0, 0.76)));
	background:	-webkit-linear-gradient(top left, rgba(0, 0, 0, 0.76), rgba(0, 0, 0, 0.76));
