.navbar {
	display: -ms-flexbox;
	display: flex; /* or inline-flex */
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.navbar-brand {
	-ms-flex-order: 0;
	    order: 0;
	font-size: 22px;
}

.navLinks {
	display: -ms-flexbox;
	display: flex; /* or inline-flex */
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: space-evenly;
	    justify-content: space-evenly;
	-ms-flex-positive: 1;
	    flex-grow: 1;
}

.link {
	-ms-flex-order: 0;
	    order: 0;
	-ms-flex-positive: 0;
	    flex-grow: 0;
	letter-spacing: 2px;
	font-size: 12px;
}

.login {
	-ms-flex-order: 2;
	    order: 2;
}

.linkStyle {
	color: rgba(255, 255, 255, 0.822);
	text-decoration: none;
}

.linkStyle:active, .linkStyle:hover{
	color: rgb(255, 255, 255);
	text-decoration: none;
}

.navbar {
	background-color: rgb(0, 0, 0);
	background:	-webkit-gradient(linear, left top, right bottom, from(rgba(0, 0, 0, 0.76)), to(rgba(0, 0, 0, 0.76)));
	background:	-webkit-linear-gradient(top left, rgba(0, 0, 0, 0.76), rgba(0, 0, 0, 0.76));
	background:	-o-linear-gradient(top left, rgba(0, 0, 0, 0.76), rgba(0, 0, 0, 0.76));
	background:	linear-gradient(to bottom right, rgba(0, 0, 0, 0.76), rgba(0, 0, 0, 0.76));
	margin-bottom: 20px;
}



