.subLinks{
	display: -ms-flexbox;
	display: flex; /* or inline-flex */
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}

.link {
	-ms-flex-order: 0;
	    order: 0;
	-ms-flex-positive: 0;
	    flex-grow: 0;
	letter-spacing: 2px;
	font-size: 12px;
	margin-top: 15px;
	margin-bottom: 25px;
	margin-left: 15px;
	margin-right: 15px;
}

.linkStyle {
	color: rgba(255, 255, 255, 0.822);
	text-decoration: none;
}

.linkStyle:active, .linkStyle:hover{
	color: rgb(255, 255, 255);
	text-decoration: none;
}



