.jobCardHolder {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: space-evenly;
	    justify-content: space-evenly;
	margin-left: 20px;
	margin-right: 20px;
}

.card {
	-ms-flex-order: 0;
	    order: 0;
	margin-top: 20px;
	margin-left: 5px;
	margin-right: 5px;
	background-color: rgba(46, 173, 167, 0.705);

}

.jobLink {
	color: rgba(255, 255, 255, 0.822);
	text-decoration: none;
}
.jobLink:active, .jobLink:hover{
	color: rgb(255, 255, 255);
	text-decoration: none;
}

.jobCard {
	margin-bottom: 20px;
}

.cardCSS {
	width: 360px !important;
	-ms-flex-order: 0;
	    order: 0;
	margin-right: 39px;
	background-color: rgba(46, 173, 167, 0.705);
	padding-bottom:20px;
}

.cardsCSS {
	margin-bottom: 20px !important;
}


