.jumbotron {
border-radius: 0;
background:	-webkit-gradient(linear, left top, right bottom, from(rgba(255, 255, 255, 0.301)), to(rgba(255, 255, 255, 0.301)));
background:	-webkit-linear-gradient(top left, rgba(255, 255, 255, 0.301), rgba(255, 255, 255, 0.301));
background:	-o-linear-gradient(top left, rgba(255, 255, 255, 0.301), rgba(255, 255, 255, 0.301));
background:	linear-gradient(to bottom right, rgba(255, 255, 255, 0.301), rgba(255, 255, 255, 0.301));
}


